}

input::-ms-reveal {
  width: 0;
  height: 0;
  display: none;
}

select::-ms-expand {
  display: none;
}

.bg-blue-gradient {
  position: relative;
}

.bg-blue-gradient:before {
  content: "";
  display: block;
  background: -webkit-gradient(linear, left top, left bottom, from(#bfd6e5), color-stop(#e7f0f6), to(#FFF));
  background: -webkit-linear-gradient(top, #bfd6e5 0%, #e7f0f6, #FFF 16%);
  background: -o-linear-gradient(top, #bfd6e5 0%, #e7f0f6, #FFF 16%);
  background: linear-gradient(to bottom, #bfd6e5 0%, #e7f0f6, #FFF 16%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.4;
}

.bg-blue-gradient--internal:before {
  content: "";
  display: block;
  background: -webkit-gradient(linear, left top, left bottom, from(#bfd6e5), color-stop(#e7f0f6), to(#FFF));
  background: -webkit-linear-gradient(top, #bfd6e5 0%, #e7f0f6, #FFF 50px);
  background: -o-linear-gradient(top, #bfd6e5 0%, #e7f0f6, #FFF 50px);
  background: linear-gradient(to bottom, #bfd6e5 0%, #e7f0f6, #FFF 50px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 1;
}

.bg-blue-gradient__inner {
  position: relative;
  z-index: 1;
}

