}

#header .nav ul li:before {
  content: "";
  display: block;
  position: absolute;
  top: -90px;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  min-height: 70px;
  background: rgba(0, 0, 0, 0.1);
  opacity: 0;
  transition: all 0.4s;
}

#header .nav ul li a {
  display: block;
  position: relative;
  z-index: 2;
}

#header .nav ul li.active {
  color: #FFDE00;
}

#header .nav ul li:hover {
